import React, { useState } from "react";
import {
  Col,
  Card,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  Button,
} from "reactstrap";
import AddNewJob from "./AddNewJob";
import JobDetails from "./JobDetails";

function JobList({ jobs, admin = false, editJob }) {
  const [detailJobIndex, setDetailJobIndex] = useState(-1);
  const [edit, setEdit] = useState(false);
  return (
    <>
      {jobs.map((job, i) => (
        <Col key={i} sm={8} md={6} className="mx-auto my-1">
          <Card>
            <CardBody>
              <CardTitle tag="h5">
                <strong>{job.name}</strong>
              </CardTitle>
              <CardSubtitle tag="h6" className="mb-2 text-muted">
                {job.pay && `$${job.pay}`}
              </CardSubtitle>
              <CardText>{job.description.substr(0, 40)} . . . .</CardText>
              <div className="d-flex justify-content-between align-items-center">
                <Button color="info" onClick={() => setDetailJobIndex(i)}>
                  Details
                </Button>
                {admin && (
                  <Button color="info" onClick={() => setEdit(!edit)}>
                    Edit
                  </Button>
                )}
                {admin && <Button color="danger">Delete</Button>}
                {admin && edit && (
                  <AddNewJob
                    jobDetails={job}
                    editJob={editJob}
                    toggleAddNewJob={() => setEdit(!edit)}
                  />
                )}
              </div>
            </CardBody>
          </Card>
        </Col>
      ))}
      {detailJobIndex !== -1 && (
        <JobDetails
          job={jobs[detailJobIndex]}
          setDetailJobIndex={setDetailJobIndex}
        />
      )}
    </>
  );
}

export default JobList;
